<template>
    <div>
        <div class="page-breadcrumb">
            <router-link
                class=""
                :to="{ name: 'clients' }"
            >
                Clients /
            </router-link>
        </div>
        <h3 class="page-title">{{ title }}</h3>
        <div v-if="isFetching">
            Loading... <preloader size="40px"></preloader>
        </div>
        <b-form v-if="!isFetching" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <b-form-group
                label="Start Date"
                class="date-picker"
                label-for="inputStartDate"
                v-bind:class="{ 'is-invalid': $v.form.start_date.$error }"
            >
                <date-picker
                    id="inputStartDate"
                    v-model="$v.form.start_date.$model"
                    lang="en"
                    format="YYYY-MM-DD"
                ></date-picker>
                <div class="invalid-feedback" v-if="$v.form.start_date.$error">
                    This field is required
                </div>
            </b-form-group>
            <b-form-group label="End Date" label-for="inputEndDate">
                <date-picker
                    id="inputEndDate"
                    v-model="$v.form.end_date.$model"
                    lang="en"
                    format="YYYY-MM-DD"
                ></date-picker>
            </b-form-group>
            <b-form-group label="From number" label-for="inputFromNumber">
                <b-form-input
                    v-model="$v.form.from_number.$model"
                    id="inputFromNumber"
                    v-bind:class="{ 'is-invalid': $v.form.from_number.$error }">
                    ></b-form-input>
                <b-form-invalid-feedback>
                    Phone number must contain 10 digits
                </b-form-invalid-feedback>
            </b-form-group>
            <b-form-group label="To number" label-for="inputToNumber">
                <b-form-input
                    v-model="$v.form.to_number.$model"
                    id="inputToNumber"
                    v-bind:class="{ 'is-invalid': $v.form.to_number.$error }">
                    ></b-form-input>
                <b-form-invalid-feedback>
                    Phone number must contain 10 digits
                </b-form-invalid-feedback>
            </b-form-group>
            <b-button :disabled="isSaving" type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
            <preloader v-if="isSaving" size="35px" class="ml-3"></preloader>
        </b-form>
    </div>
</template>

<script>
import { required, email, url, numeric, minLength, maxLength } from 'vuelidate/lib/validators';
import DatePicker from 'vue2-datepicker';
import moment from 'moment';
import Backend from '../../services/backend';
import Preloader from '../Preloader';
const backend = new Backend();

export default {
    name: 'PhoneRangesAddEditPage',
    components: {
        Preloader,
        DatePicker
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {},
            title: '',
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null
        };
    },
    created() {
        this.handlerForRouteChange();
    },
    props: [
        'pageType',
        'id',
        'subId'
    ],
    validations: {
        form: {
            start_date: {
                required
            },
            end_date: {

            },
            from_number: {
                required,
                numeric,
                minLength: minLength(10),
                maxLength: maxLength(10)
            },
            to_number: {
                required,
                numeric,
                minLength: minLength(10),
                maxLength: maxLength(10)
            }
        }
    },
    methods: {
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Edit Phone ranges' : 'Add Phone ranges';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                let subId = this.$route.params.subId;
                backend.getRequest('vbill/clients/' + id + '/phone_ranges/' + subId)
                    .then((response) => {
                        this.form = response.data;
                        this.isFetching = false;
                        this.errorFetching = false;
                    })
                    .catch((error) => {
                        this.errorFetching = true;
                        this.isFetching = false;
                        this.errorMessage = error.response.data.name && error.response.data.name[0] ||
                            'Something went wrong. Please try again later';
                    });
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = Object.assign({}, this.form);
            if (data.start_date) {
                data.start_date = moment(data.start_date).format('YYYY-MM-DD');
            }
            if (data.end_date) {
                data.end_date = moment(data.end_date).format('YYYY-MM-DD');
            }
            this.isSaving = true;
            let id = this.$route.params.id;
            let subId = this.$route.params.subId;
            if (this._props.pageType === 'add') {
                backend.postRequest('vbill/clients/' + id + '/phone_ranges/', data)
                    .then(() => {
                        this.errorMessage = null;
                        this.successMessage = `Phone ranges were added successfully`;
                        this.isSaving = false;
                        this.clearForm();
                    })
                    .catch((error) => {
                        this.isSaving = false;
                        this.successMessage = null;
                        this.errorMessage = error.response.data.error ||
                            'Something went wrong. Please try again later';
                    });
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('vbill/clients/' + id + '/phone_ranges/' + subId + '/', data)
                    .then(() => {
                        this.hasBackendError = false;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.successMessage = `Phone ranges were changed successfully`;
                    })
                    .catch((error) => {
                        this.isSaving = false;
                        this.successMessage = null;
                        this.errorMessage = error.response.data.error ||
                            'Something went wrong. Please try again later';
                    });
            }
        },
        clearForm() {
            this.form = {
                start_date: null,
                end_date: null,
                from_number: null,
                to_number: null
            };
            setTimeout(() => this.$v.$reset(), 0);
        },
        fetchErrorHandler(error) {
            this.errorFetching = true;
            this.isFetching = false;
            this.errorMessage = error.response.data.error ||
                'Something went wrong. Please try again later';
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
    .date-picker.is-invalid .invalid-feedback {
        display: block;
    }
</style>
